
#movie {
    width: 100%;
}
#movie h3.playing {
    padding: 20px 0 0 4%;
    color: #494949;
    display: block;
    width: 300px;
}
.grid {
    padding: 20px 0;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    box-sizing: border-box;
    display: block;
    float: left;
}
#list .item {
    text-decoration: none;
    color: #9b9b9b;
}
.grid .item {
    float: left;
    box-sizing: border-box;
    width: 33.33333%;
    padding-left: 4%;
    padding-right: 4%;
    margin-bottom: 10px;
    overflow: hidden;
}
.grid .item .info h3 {
    width: 100%;
    font-size: 12px;
    display: block;
    word-break: keep-all;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 5px 0;
    font-weight: 400;
}
.cover {
    height: 150px;
}
.cover img {
    height: 150px;
    width: 100%;
}





