
#pview{
    width:100%;
    min-height:568px;
    height:100%;
    height:700px;
    background: #000;
    text-align: center;
    vertical-align: middle;
    display: table;
    table-layout: fixed;
}
#pview img{
    width:100%;
    text-align: center;
    vertical-align: middle;
    display: block;
}



