
#movie-detail {
  width: 100px;
  padding: 10px 0;
  margin: 0 2%;
  width: 96%;
  position: relative;
  box-sizing: border-box;
  height: auto;
  overflow: hidden;
  border-bottom: 1px solid #d7d7d7;
}
.movie_picture {
  width: 35%;
  float: left;
}
.movie_picture img {
  width: 100%;
}
.mv_info {
  float: left;
  width: 65%;
  overflow: auto;
  box-sizing: border-box;
  padding-left: 6%;
  padding-top: 10px;
}
.mv_info .name {
  font-size: 16px;
  line-height: 30px;
  width: 100%;
  display: block;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: keep-all;
  word-wrap: normal;
}
.scoreBar {
  line-height: 30px;
}
.star_bg {
  display: inline-block;
  width: 120px;
  background: url(http://pimg1.126.net/movie/images/m/wap/start_bg.png) left top no-repeat;
  height: 20px;
  background-size: auto 20px;
}
.star {
  display: block;
  background: url(http://pimg1.126.net/movie/images/m/wap/start.png) left top no-repeat;
  height: 20px;
  background-size: auto 20px;
}
.score {
  color: #7a6852;
  display: inline-block;
  font-size: 14px;
  height: 20px;
  line-height: 20px
}
.mvDescList {
  color: #666;
  line-height: 26px;
  font-size: 14px;
}
.mvDescList {
  color: #666;
  padding: 10px;
  float: left;
  box-sizing: border-box;
  width:100%;
  height:auto;
  overflow: hidden;
}
.mvDescList li {
  padding-left: 3.1em;
}
.mvDescList li i{
  font-style: normal;
}
.mvDescList span {
  width: 3.1em;
  display: inline-block;
  margin-left: -3.1em;
}
#moviePart{
  width:100%;
  float: left;
  padding:10px 0;
}
.jqHead {
  height:40px;
  line-height: 40px;
  background: #f5f5f5;
  border-top: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
  padding-left: 1rem;
  font-size: 1rem;
  color: #333;
}
.mvIntroBox {
  line-height: 24px;
  font-size:14px;
  margin:10px;
  word-break: break-all;
  white-space: normal;
  color: #666;
}



