
#login {
    padding-top: 100px;
    background: url("https://dn-coding-net-production-pp.qbox.me/757014a2-76bc-4da7-bcb4-5fd79186fc50.png");
    box-sizing: border-box;
    height: 100%;
    min-height: 100%;
    width: 100%;
    margin-top: -50px;
    background-size: cover;
}
#form {
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    margin-top: 50px;
}
#form li {
    padding: 10px 0;
}
#form li input {
    width: 100%;
    height: 40px;
    outline: none;
    background: transparent;
    -webkit-appearance: none;
    border: 1px solid #DDD;
    border-radius: 8px;
    box-sizing: border-box;
    padding: 10px;
    font-size: 16px;
    color: #444;
}
#login_btn {
    padding: 30px 10px;
}
.msg {
    text-align: center;
    color: #666
}



