
.container {
        width: 100%;
        margin: 0 auto;
        background: #f6f6f6;
        padding-top: 10px;
}
.waterfall {
        -moz-column-count: 2;
        column-count: 2;
        -moz-column-width: 50%;
             column-width: 50%;
        -moz-column-gap: 0px;
             column-gap: 0px;
}
.waterlist {
        padding: 0 5px;
        margin: 7px;
        page-break-inside: avoid;
             break-inside: avoid;
        background: white;
}
.waterlist img {
        width: 100%;
        margin-bottom: 5px;
        border-bottom: 1px solid #cccccc;
}
.waterlist p {
        font-size: 14px;
}
#load {
        display: block;
        padding: 15px 0;
        outline: 0;
        width: 100%;
        text-align: center;
        z-index: 99;
        margin: 0 auto;
}
#bigbox{
  margin:0 auto;
  text-align: center;
  background: #000;

  left:0;
  top:0;
}
#bigbox   .v-modal{
      background: #000 !important;
}
#bigbox img{
  width:100%;
  max-width: 750px
}
#bigbox  img[lazy=loading] {
        width: 100%;
        margin: auto;
}



