
.m-intr {
    position: relative;
    margin: 0 10px 5px 15px;
    font-size: 14px;
    line-height: 19px;
    float: left;
    font-weight: normal;
    font-style: normal;
    max-height:135px;
  overflow: hidden;
}
.m-intr .tags {
    margin: 10px 0;
    color: #666;
}
.m-intr .tag {
    margin-right: 10px;
    padding: 1px 7px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 9999px;
    font-size: 12px;
    font-style: normal;
}
.m-intr .desc {
    margin-top: 10px;
    padding-bottom: 18px;
    word-wrap: break-word;
    white-space: normal;
    color: #666;
}



