
.coding{
    background: red;
    width:100%;
    height:auto;
}
li.coding_list {
        padding:10px;
        box-sizing: border-box;
        width:100%;
        float: left;
        border-bottom:10px solid #FAFAFA;
}
.coding_list .coding_header img{
        float: left;
        width:40px;
        height:40px;
        border-radius:50%;
        margin-right:10px;
}
.coding_header p{
        font-size:12px;
        color: #666;
        line-height:20px;
}
.coding_header strong{
        font-size:14px;
        line-height:20px;
}
.code_content{
        width:100%;
        padding:10px 0 0 0;
}
.code_content p{
    width:100%;
    font-size:14px;
    line-height:24px;
}
img.emoji{
       display: inline-block;
        width:20px;
        margin-top:-3px;
}
.code_content img.bubble-markdown-image{
        width:70%;
        padding:5px 0 0 0;
}
.topic{
    color: #2D59A2;
    font-size:13px;
}
.device{
        font-size:10px;
        color: #999;
}



